@use "base/variables" as v;
@use "base/mixins" as m;

.login {
    background-color: v.$gray;

    header {
        height: 10rem;
        background-color: v.$white;
        margin-top: 0;

        .logo {
            background-color: v.$white;
            height: 100%;

            img {
                height: 100%;
                width: auto;
                background-color: v.$white;
            }
        }
    }

    img,
    .login-form {
        display: flex;
        align-items: center;
        justify-content: center;
        height: calc(100vh - 10rem);
        .blue-form {
            background-color: v.$blue;
            border-radius: 1rem;
        }
        .pink-form {
            background-color: v.$pink;
        }
        form {
            width: 80vw;
            max-width: 40rem;
            margin: 0 auto;
            padding: 3rem;
            padding-top: 5rem;
            margin: auto;

            .form-control {
                font-size: calc(1rem + 0.25vw);
            }
            .form-check-label {
                font-size: calc(0.75rem + 0.25vw);
                font-style: italic;
            }

            label {
                color: v.$white;
            }

            button {
                font-size: calc(1rem + 0.25vw);
                border-radius: 1rem;
                background-color: v.$white;
                color: v.$black;
                transition: all 0.3s ease-in-out;
                margin: 2rem 0;

                &:hover {
                    background-color: darken($color: v.$gray, $amount: 15%);
                    transition: all 0.3s ease-in-out;
                }
            }
        }
    }
}