@use "base/variables" as v;
@use "base/mixins" as m;

.header-bills {
    z-index: 2;
}

.super-admin-body {
    display: flex;
    margin: 0 5rem 5rem 40rem;
    padding-bottom: .5rem;
    padding-right: 2rem;
    flex-direction: column;
    width: 20vw;

    .login-form {

        .form-control {
            width: 100%;
            margin-bottom: 1rem;
        }

        .btn {
            width: 100%;
            padding: 0.5rem 1rem;
        }

        input {
            font-size: calc(1rem + 0.25vw);
        }
    }

    .error-msg {
        font-size: calc(1rem + 0.25vw);
        color: v.$red;
        font-weight: bold;
        text-align: center;
    }

    .buttons {
        display: flex;
        flex-direction: column;
        justify-content: start;
        margin-top: 1rem;
        width: fit-content;

        a {
            padding: 1.5rem 0;
            padding-right: 10rem;
            margin-bottom: 4rem;
            font-size: calc(1rem + 0.25vw);
            text-align: left;
            background-color: v.$blue;
            padding-left: 1rem;
            border-radius: 1rem;
            &:hover {
                background-color: darken($color: v.$blue, $amount: 10);
            }
        }

        a {
            display: block;
            text-align: left;
            margin-left: 1rem;
            text-decoration: none;
            color: v.$white;
        }
    }
}

.bills {
    justify-content: space-between;
    flex-direction: row;
    width: unset;
    .form-info {
        margin-bottom: 5rem;
    }
    form {
        width: 100%;
        button {
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    label {
        margin-right: 2rem;
    }

    label,
    input {
        display: inline-block;
        width: fit-content;
        font-size: calc(0.75rem + 0.25vw);
    }

    .bills-info-radio {
        display: unset;
        &>* {
            display: flex;
            align-items: center;
            label {
                padding-left: 1rem;
            }
        }
    }

    .bills-info {
        display: grid;
        gap: 1rem;
    }

    .title {
            font-size: calc(1.25rem + 0.25vw);
            font-weight: bold;
            text-transform: uppercase;
            margin: 2rem 0;
            color: v.$blue;
            width: 100%;
        }

    .bills-info-1 {
        grid-template-columns: repeat(1, 1fr);
    }

    .bills-info-2 {
        grid-template-columns: repeat(2, 1fr);
        input {
            width: 7rem;
        }
    }

    .bills-info-3 {
        grid-template-columns: repeat(3, 1fr);
    }

    .bills-info-img {
        div {
            img {
                display: block;
                width: 20rem;
                height: 20rem;
                height: auto;
                margin-bottom: 2rem;
                object-fit: contain;
            }
        }
    }

    .required-fields {
        width: 100%;
        text-align: right;
        font-style: italic;
        font-size: calc(0.75rem + 0.25vw);
    }
}